@import url("https://fonts.googleapis.com/css2?family=Archivo:wght@100;200;300;400;500;600;700&display=swap");
:root {
  --gray: #7f7c7a;
  --cream: #f8f5f0;
  --bg-input: #f7f7f7;
}

:root {
  --font-archivo: "Archivo", sans-serif;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Archivo", sans-serif;
  position: relative;
  font-size: 1.125rem;
}

a {
  text-decoration: none;
  color: var(--gray);
}

input {
  border: none;
  padding: 1.25rem;
  background-color: var(--bg-input);
  font-family: inherit;
  font-size: inherit;
}

button {
  cursor: pointer;
}

.bg-cream {
  background-color: var(--cream);
}

.bg-dark {
  font-family: inherit;
  font-weight: 700;
  font-size: 1.05rem;
  border: none;
  color: white;
  background: black;
  border-radius: 5px;
  padding: 1rem;
}

.header {
  padding: 2rem 8rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1.5rem;
}
.header .logo > img {
  display: block;
  height: 100%;
}

.container {
  padding-inline: 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

h1 {
  font-size: 3.4375rem;
  margin-bottom: 0.3em;
}

h2 {
  font-size: 2.5rem;
  margin-bottom: 1em;
}

h3 {
  font-size: 1.5625rem;
  margin-bottom: 1em;
}

p {
  color: var(--gray);
  margin-bottom: 1.5em;
}

q {
  line-height: 1.8;
}

:root {
  --gray: #7f7c7a;
}

p {
  font-size: 1.125rem;
}

ul {
  list-style: none;
  margin-bottom: 1.5em;
  display: flex;
  flex-direction: column;
  gap: 0.5em;
}

li {
  color: #7f7c7a;
  display: flex;
  align-items: center;
  gap: 1em;
  font-size: 1.125rem;
}

a {
  display: block;
}

img {
  display: inline-block;
}

.hero__text {
  max-width: 55%;
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.hero__text > div {
  margin-inline: auto;
  max-width: 28.125rem;
}
.hero__text > div > a {
  max-width: max-content;
  margin-bottom: 2em;
}

.partners > * {
  color: black;
}

.hero__image {
  background: no-repeat center center/cover url("/Assets/Hero Image.jpg");
  height: 100vh;
  width: 45%;
  position: absolute;
  top: 0;
  right: 0;
}

.footer {
  height: 10vh;
}

.header > a:first-of-type {
  margin-right: auto;
}

.header-signin {
  max-width: 60vw;
}

.hero__form {
  max-width: 60%;
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.hero__form > div {
  margin-inline: auto;
  max-width: 37.5rem;
}
.hero__form > div form {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}
.hero__form > div .create {
  display: flex;
  gap: 0.3125rem;
  font-size: 1.125rem;
}
.hero__form > div .create a {
  text-decoration: underline;
  color: #18afdd;
  font-weight: 700;
}

.hero__review {
  height: 100vh;
  width: 40%;
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: var(--cream);
}
.hero__review > div {
  margin-inline: auto;
}
.hero__review h3 {
  max-width: 18.75rem;
}
.hero__review .testimonial {
  display: grid;
  max-width: max-content;
  grid-template-columns: max-content auto;
  grid-template-rows: repeat(2, 1fr);
  grid-template-areas: "avatar name" "avatar subtitle";
  align-items: center;
  column-gap: 1rem;
}
.hero__review .testimonial .avatar {
  grid-area: avatar;
  height: 100%;
}
.hero__review .testimonial .name {
  grid-area: name;
  margin-bottom: 0;
  font-weight: 700;
  color: black;
}
.hero__review .testimonial .subtitle {
  grid-area: subtitle;
  margin-bottom: 0;
}